.header {
  padding: 40px 7.2% 0 7.2%;
  min-height: 100vh;
  background: url(../imgs/bg-header.jpg);
  background-color: rgba(39, 42, 50, 0.8);
  background-size: cover;
  background-repeat: no-repeat;
}

.header__top {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.header__top .logo {
  width: 115px;
  height: 20px;
}

.header .nav__menu-item {
  list-style-type: none;
  display: inline-block;
}

.header .nav__menu-item-link {
  color: #fffefe;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 400;
  vertical-align: middle;
  padding-top: 1px;
  display: block;
  width: 85px;
  height: 30px;
  text-align: center;
  text-decoration: none;
  position: relative;
  border: 2px solid transparent;
}

.header .nav__menu-item-link:hover {
  border: 2px solid #fff;
  border-radius: 16px;
  transform: scale(1.07);
  transition: all 0.7s;
}

@media all and (max-width: 700px) {
  .header .nav__menu-item {
    margin-right: 0;
    display: block;
    margin-bottom: 5px;
  }
  .header .header__img-link {
    margin-bottom: 20px;
  }
}

.header__title {
  max-width: 600px;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}

.header__title h1 {
  color: #fff;
  font-family: "Source Sans Pro";
  font-size: 35px;
  font-weight: 300;
  text-transform: uppercase;
  min-width: 380px;
  margin: 225px 0 40px 0;
  letter-spacing: 7px;
}

.header__title p {
  color: #fffefe;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
}

@media all and (max-width: 700px) {
  .header .header__title h1 {
    margin-top: 90px;
  }
}

.header__img-link {
  width: 35px;
  height: 35px;
  display: block;
  border-radius: 50%;
  margin: 10% auto 0 auto;
}

.header__img-link:hover {
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.7);
  transform: scale(1.07);
  transition: all 0.5s;
}
